<template>
  <div class="about-modal__background">
    <transition name="slide-fade" :appear="true">
      <div class="about-modal__content">
        <transition name="slide-fade3" :appear="true">
          <div class="about-modal__close" @click="closeModal">
            &times;
          </div>
        </transition>
        <div class="about-modal__content--logo">
          <transition name="slide-fade3">
            <img
              v-if="counter === 0"
              v-bind:src="`${modalImg1}`"
              class="about-modal__content--logo-img"
              :alt="`${modalAlt}`"
            />
          </transition>
          <transition name="slide-fade3">
            <img
              v-if="counter === 1"
              v-bind:src="`${modalImg2}`"
              class="about-modal__content--logo-img"
              :alt="`${modalAlt}`"
            />
          </transition>
          <transition name="slide-fade3">
            <img
              v-if="counter === 2"
              v-bind:src="`${modalImg3}`"
              class="about-modal__content--logo-img"
              :alt="`${modalAlt}`"
            />
          </transition>
        </div>

        <h1 class="about-modal__content--title">{{ modalTitle }}</h1>

        <p class="about-modal__content--paragraph1">
          {{ modalText }}
        </p>

        <button
          class="about-modal__content--nextBtn"
          @click="nextPhoto"
          v-if="modalCarousel && counter <= 1"
        >
          &rarr;
        </button>
        <button
          class="about-modal__content--prevBtn"
          @click="prevPhoto"
          v-if="modalCarousel && counter >= 1"
        >
          &larr;
        </button>

        <!-- <p class="about-modal__content--paragraph2">
            </p> -->
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "Modal",

  props: {
    modalCarousel: {
      type: Boolean,
      required: false,
    },

    modalImg1: {
      type: String,
      required: false,
    },

    modalImg2: {
      type: String,
      required: false,
    },

    modalImg3: {
      type: String,
      required: false,
    },

    modalTitle: {
      type: String,
      required: true,
    },

    modalText: {
      type: String,
      required: true,
    },

    modalAlt: {
      type: String,
      required: false,
    },

    counter: {
      type: Number,
      required: false,
    },
  },

  methods: {
    closeModal() {
      this.$emit("closeModal");
    },
    nextPhoto() {
      this.$emit("nextPhoto");
    },
    prevPhoto() {
      this.$emit("prevPhoto");
    },
  },
};
</script>

<style lang="scss">
@import "SASS/base/_Projects.scss";
</style>
